import React from 'react';
//函数 数组任何数据都在props中
const Buttons = (props)=>{
    const btns = [1,2,3,4,5];
    {
        return <div>{
            btns.map(
                (item)=>(
                  <button 
                    key={item}  onClick = {()=>props.getData(item)}
                  >
                    {item}
                  </button>
              ))
            }

        </div>
       
      }
}


export default Buttons;