import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Context from './context'
import Demo02 from './Demo02'

//小练习 对于行内加样式

        const Demo01 = (props)=>{
            return (
                <div style={{width:100,height:'100px',backgroundColor:'red'}}>
                    <h2>demo01</h2>
                    <ul>
                        <Context.Consumer>
                        {
                            data=>data.data.map((item)=><li key={item}>{item}</li>)
                        }
                        </Context.Consumer>
                    </ul>
                    <Demo02/>
                        {/* 这里引用就直接写就行 */}
                </div>
            )
        }
        
    

Demo01.protoTypes ={
    name:PropTypes.string.isRequired
    //必须传的  
}
Demo01.defaultProps={
    name:'tomg'
    // 就是你没有传参数的时候 给你一个默认的。
}
export default Demo01
