import React from 'react'

const Login = (props) => {
    const login = ()=>{
        localStorage.setItem('userinfo','tom');
        // 就是一跳转到这个页面就会把信息存起来
        // props.history.push('/');//存完以后 跳到首页
        // props.history.replace('/');//什么不再返回来？
        props.history.replace(props.location.state);//这个值是个对象 里面有pathname
        console.log(props);

    }
    return (
        <div>
            <button onClick={login}>登录</button>
        </div>
    )
}

export default Login
