import React, { Component } from 'react'
import PropTypes from 'prop-types'
//高阶函数 接受一个组件返回一个新的组件
const withFetch = (Com,url)=>{
    return class Topic extends Component{
        constructor(){
            super();
            this.state = {
                data:[]
            }
        }
        componentDidMount(){
            fetch(url)
            .then(res=>res.json())
            .then(res=>this.setState({data:res.data}))
        }
        render(){
            return <Com data={this.state.data}/>
        }
    }
}
const De =(props)=>{
    return <div>
        <h1>标题</h1>
        {/* <div>{props.data.content}</div> */}
        <div dangerouslySetInnerHTML={
            {__html:props.data.content}
            // 这里是获取到了Com 组件中data属性的内容
        } ></div>
    </div>
}
export const Detail = withFetch(De,'https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312');

