import React,{Component, useEffect} from 'react'
import {NavLink,Link,Switch, Route, Redirect,useHistory,useLocation,useParams,useRouteMatch, withRouter,BrowserRouter as Router} from 'react-router-dom'
import './doc.css'
import RouteWithSubRoutes from './RouteWithSubRoutes'
const Doc = ({routes}) => {
    console.log(routes);
    return (
        <div className='doc'>
            <div className="content">
                <Redirect from='/doc' to='/doc/core' /> 
                {routes.map((route, i) => (
                    <RouteWithSubRoutes key={i} {...route} />
                ))}      
            </div>
            <ol>
                <li><NavLink to='/doc/core' activeStyle={{fontWeight:"bolder"}}>核心概念</NavLink></li>
                <li><NavLink to='/doc/guide' activeStyle={{fontWeight:"bolder"}}>高级指引</NavLink></li>
                <li><NavLink to='/doc/api' activeStyle={{fontWeight:"bolder"}}>API</NavLink></li>
                <li><NavLink to='/doc/hooks'activeStyle={{fontWeight:"bolder"}}>Hooks</NavLink></li>
            </ol> 
        </div>
    )
}
export default withRouter(Doc)
