import './App.css';
import React from 'react';
import {BrowserRouter as Router,Route,Link,Switch,NavLink,Redirect} from 'react-router-dom'
import Header from './pages2/Header';
import routes from './router.config';
import RouteWithSubRoutes from './pages2/RouteWithSubRoutes'
export const PrivateRoute=({ component:Com, ...rest })=> {
	return (
	  <Route
		{...rest}
		render={
		   ({ location }) =>
		   localStorage.getItem('userinfo')
		   ?<Com {...rest} />  //这里相当于给route传了参数
		   : (
			<Redirect
			  to={{
				pathname: "/login",
				state: location 
			  }}
			/>
		  )
		}
	  />
	);
}
const App = ()=>{
	return <Router>
				<Header />
                <Switch>
					{routes.map((route, i) => (
						<RouteWithSubRoutes key={i} {...route} />
					))}
                </Switch>
            </Router>
}
export default App 