// 路由配置的文件 是一个数组
import Home from './pages2/Home';
import Doc from './pages2/Doc';
import Tuto from './pages2/Tuto';
import Blog from './pages2/Blog';
import Comm from './pages2/Comm';
import Login from './pages2/Login';
import Core from './pages2/Core';
import Guide from './pages2/Guide';
import Api from './pages2/Api';
import Hooks from './pages2/Hooks';
const routes = [
    {
        path:'/',
        exact:true,
        component:Home

    },
    {
        path:'/doc',
        component:Doc,
        pri:true, //这里代表是权限路由，
        //子组件
        routes:[
            {
                path:'/doc/core',
                component:Core,
            },
            {
                path:'/doc/guide',
                component:Guide
            },
            {
                path:'/doc/api',
                component:Api
            },
            {
                path:'/doc/hooks',
                component:Hooks,
            }
        ]
    },
    {
        path:'/tuto',
        component:Tuto,
        pri:true
    },
    {
        path:'/blog',
        component:Blog
    },
    {
        path:'/comm',
        component:Comm
    },
    {
        path:'/login',
        component:Login
    }
]
export default routes;
