import React,{Component} from 'react'; //这一个是必须写的 因为这里面用到了
import List from '../components/List'  //这里也要去引用List
import Buttons from '../components/btns';
class Home extends React.Component{
    constructor(props){
      super(props);
      this.state = {
        data:[]
      }
      // this.handleClick = this.handleClick.bind(this)
    }
  componentDidMount(){
      fetch('https://cnodejs.org/api/v1/topics')
      .then(res=>res.json())
      .then( ({data}) => {
        console.log(data);
        this.setState({data})
      })
    }
  getData=(page)=>{
    console.log(page);
    fetch('https://cnodejs.org/api/v1/topics?page='+page).
    then(res=>res.json()).
    then(({data})=>{
    console.log(data);
    this.setState({data}); //这里拿到json数据
  })
  }
    render(){
      const { data } = this.state;
      return <div>
          
        <Buttons getData = {this.getData}/>
        <List data={data}/>
      </div>
    }
  }
  //命名导出 默认导出 主要看react都是默认导出不用取名    
  //默认导出 只能导出一个
  export default Home; //这里是默认导出  

